.App {
  text-align: center;
}

.icons-wrapper {
  display: block;
  text-align: center;
}

.icons-content {
  display: inline-block;
  margin: 0 auto;
}

.game-icon {
  display: inline-block;
  height: 20vmin;
  float: left;
  margin: 0 1vmin;
  cursor: pointer;
}

.arcade {
  padding-top: 15vmin;
  height: 40vmin;
  pointer-events: none;
}

.tag-line {
  height: 10vmin;
  line-height: 10vmin;
  font-weight: bolder;
}
.App-header {
  background-color: cornflowerblue;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
